.card {
    max-width: 800px;
    min-height: calc(220px);
    margin-top: 15px;
    height: 93px;
    .personalInfoRight {
        display: flex;
        flex: 0 0 40%;
        img {
            width: 133px;;
            margin-left: 12px;
            border-radius: 5px;
            cursor: pointer;
        }
        .personalDetailsRight {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }
    .cardContent {
        font-size: 14px;;
    }
    .personalInfoLeft {
        flex: 0 0 60%;
        border-right: 1pt solid rgba(0,0,0,.18);
        padding-right: 12px;
            .mySelfDescBox {
                margin-top:15px;
                & > span {
                    height:75px;
                    display:block;
                    overflow:hidden;
                }
            }
            .workDays {
                margin-top: 7px;
            }
    }


    .cardActions {
        justify-content: flex-end;
    }
}