*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;;
}

/* GLOBALS */

.container {
  max-width: 1024px;;
  margin: 0 auto;;
}

.text-opacity {
  opacity: .54;
}


/* HELPERS */
 
.flex {
  display: flex;;
}

.column {
  flex-direction: column;
}

.justify-content-center {
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.full-width {
  width: 100%;
}

.MuiPaper-root {
  /* min-height: 500px; */
}


/* AUTOCOMPLETE */

.autocmplete-style-dialog {
  width: 70%;
}

.navbar-app {
  height: 42px;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,.54);
}